<template>
  <transition name="slide">
    <div class="add-song" @click.stop v-show="showFlag">
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="searchBox" placeholder="搜索歌曲" @query-change="queryChange"></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :switches="switches" :currentIndex="currentIndex" @switch="switchIndex"></switches>
        <div class="list-wrapper">
          <scroll
            ref="songList"
            class="list-scroll"
            :data="laterPlayList"
            v-if="currentIndex === 0"
          >
            <div class="list-inner">
              <song-list :songs="laterPlayList" @select="selectSong"></song-list>
            </div>
          </scroll>
          <scroll
            ref="searchList"
            class="list-scroll"
            :data="searchHistoryList"
            v-if="currentIndex === 1"
          >
            <div class="list-inner">
              <search-list
                @select-search="addQuery"
                @del-search="delSearchHistory"
                :searchHisList="searchHistoryList"
              ></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest :query="query" @select="saveSuggest"></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌曲已经添加到播放列表</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script>
import SearchBox from '@/base/search-box/search-box.vue'
import Suggest from '@/components/suggest/suggest.vue'
import Switches from '@/base/switches/switches.vue'
import SongList from '@/base/song-list/song-list'
import Scroll from '@/base/scroll/scroll.vue'
import TopTip from '@/base/top-tip/top-tip.vue'
import SearchList from '@/base/search-list/search-list.vue'
import { serachMixin } from '@/common/js/mixin.js'
import { mapGetters, mapActions } from 'vuex'
import { Song } from '@/common/js/song.js'
export default {
  mixins: [serachMixin],
  data() {
    return {
      showFlag: false,
      switches: [{ name: '最近播放' }, { name: '搜索历史' }],
      currentIndex: 0
    }
  },
  computed: {
    ...mapGetters(['laterPlayList'])
  },
  methods: {
    ...mapActions(['insertSong']),
    show() {
      this.showFlag = true
      setTimeout(() => {
        this.$refs.songList.refresh()
        this.$refs.searchList.refresh()
      }, 20)
    },
    hide() {
      this.showFlag = false
    },
    saveSuggest() {
      this.saveSearch()
      this.$refs.topTip.show()
    },
    selectSong(song, index) {
      if (index !== 0) {
        this.insertSong(new Song(song))
        this.$refs.topTip.show()
      }
    },
    switchIndex(index) {
      this.currentIndex = index
    }
  },
  components: {
    SearchBox,
    Suggest,
    Switches,
    SongList,
    Scroll,
    SearchList,
    TopTip
  }
}
</script>

<style scoped lang="less">
@import '~@/common/styles/variable';
@import '~@/common/styles/mixin';

.add-song {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 200;
  background: @color-background;

  &.slide-enter-active,
  &.slide-leave-active {
    transition: all 0.3s;
  }

  &.slide-enter,
  &.slide-leave-to {
    transform: translate3d(100%, 0, 0);
  }

  .header {
    position: relative;
    height: 44px;
    text-align: center;

    .title {
      line-height: 44px;
      font-size: @font-size-large;
      color: @color-text;
    }

    .close {
      position: absolute;
      top: 0;
      right: 8px;

      .icon-close {
        display: block;
        padding: 12px;
        font-size: 20px;
        color: @color-theme;
      }
    }
  }

  .search-box-wrapper {
    margin: 20px;
  }

  .shortcut {
    .list-wrapper {
      position: absolute;
      top: 165px;
      bottom: 0;
      width: 100%;

      .list-scroll {
        height: 100%;
        overflow: hidden;

        .list-inner {
          padding: 20px 30px;
        }
      }
    }
  }

  .search-result {
    position: fixed;
    top: 124px;
    bottom: 0;
    width: 100%;
  }

  .tip-title {
    text-align: center;
    padding: 18px 0;
    font-size: 0;

    .icon-ok {
      font-size: @font-size-medium;
      color: @color-theme;
      margin-right: 4px;
    }

    .text {
      font-size: @font-size-medium;
      color: @color-text;
    }
  }
}
</style>
